export default class Star {
  constructor(startPoint, endPoint, t) {
    this.t = t;
    this.startPoint = startPoint;
    this.endPoint = endPoint;

    this.x = startPoint.x;
    this.y = startPoint.y;
    this.xt = endPoint.x;
    this.yt = endPoint.y;
    this.xsub = Math.abs(this.x - this.xt);
    this.ysub = Math.abs(this.y - this.yt);
    this.sq = Math.sqrt(this.xsub * this.xsub + this.ysub * this.ysub);
    if ((this.x < this.xt && this.y < this.yt) || (this.x > this.xt && this.y > this.yt) || (this.y !== this.yt && this.x === this.xt)) {
      this.cos = this.ysub / this.sq;
      this.sin = this.xsub / this.sq;
    }
    if ((this.x < this.xt && this.y > this.yt) || (this.x > this.xt && this.y < this.yt) || (this.x !== this.xt && this.y === this.yt)) {
      this.cos = this.xsub / this.sq;
      this.sin = this.ysub / this.sq;
    }
    this.speed = 0.8;
    this.progress = 0;
    this.totalDistance = Math.abs(this.endPoint.y - this.startPoint.y || this.endPoint.x - this.startPoint.x);
    this.xSub = this.endPoint.x - this.startPoint.x;
    this.ySub = this.endPoint.y - this.startPoint.y;
    this.xtran = this.startPoint.x;
    this.ytran = this.startPoint.y;
  }
  draw() {
    if (this.progress <= this.sq + 20) {
      this.progress += this.speed;
      this.xtran = this.startPoint.x + this.xSub * (this.progress / this.sq);
      this.ytran = this.startPoint.y + this.ySub * (this.progress / this.sq);
    } else {
      this.progress = 0;
      this.xtran = this.startPoint.x;
      this.ytran = this.startPoint.y;
    }

    let x = this.xtran;
    let y = this.ytran;

    let xt, yt, xa, ya, xb, yb, xc, yc, recRotate, rx, ry, rw, rh;
    // a、b
    const bessel = 6;
    const sinA = this.sin * bessel;
    const cosA = this.cos * bessel;
    // c
    const sinB = this.sin * 10;
    const cosB = this.cos * 10;
    // t
    const sinC = this.sin * 20;
    const cosC = this.cos * 20;

    const degr = Math.asin(this.sin);
    rw = this.sq;
    rh = 2 * bessel;
    // 一象限
    if (this.x < this.xt && this.y > this.yt) {
      xt = x - cosC;
      xa = x - sinA;
      xb = x + sinA;
      xc = x - cosB;

      yt = y + sinC;
      ya = y - cosA;
      yb = y + cosA;
      yc = y + sinB;

      recRotate = -degr;
      rx = this.x - sinA;
      ry = this.y - cosA;
    }
    // 二象限
    if (this.x > this.xt && this.y > this.yt) {
      xt = x + sinC;
      xa = x - cosA;
      xb = x + cosA;
      xc = x + sinB;

      yt = y + cosC;
      ya = y + sinA;
      yb = y - sinA;
      yc = y + cosB;

      recRotate = -degr - Math.PI / 2;
      rx = this.x - cosA;
      ry = this.y + sinA;
    }
    // 三象限
    if (this.x > this.xt && this.y < this.yt) {
      xt = x + cosC;
      xa = x + sinA;
      xb = x - sinA;
      xc = x + cosB;

      yt = y - sinC;
      ya = y + cosA;
      yb = y - cosA;
      yc = y - sinB;

      recRotate = -degr - Math.PI;
      rx = this.x + sinA;
      ry = this.y + cosA;
    }
    // 四象限
    if (this.x < this.xt && this.y < this.yt) {
      xt = x - sinC;
      xa = x + cosA;
      xb = x - cosA;
      xc = x - sinB;

      yt = y - cosC;
      ya = y - sinA;
      yb = y + sinA;
      yc = y - cosB;

      recRotate = Math.PI / 2 - degr;
      rx = this.x + cosA;
      ry = this.y - sinA;
    }

    if (this.x === this.xt && this.y >= this.yt) {
      xt = x;
      xa = x - cosA;
      xb = x + cosA;
      xc = x;

      yt = y + cosC;
      ya = y;
      yb = y;
      yc = y + cosB;

      recRotate = -Math.PI / 2;
      rx = this.x - bessel;
      ry = this.y;
    }

    if (this.x === this.xt && this.y < this.yt) {
      xt = x;
      xa = x - cosA;
      xb = x + cosA;
      xc = x;

      yt = y - cosC;
      ya = y;
      yb = y;
      yc = y - cosB;

      recRotate = Math.PI / 2;
      rx = this.x + bessel;
      ry = this.y;
    }

    if (this.y === this.yt && this.x <= this.xt) {
      xt = x - cosC;
      xa = x;
      xb = x;
      xc = x - cosB;

      yt = y;
      ya = y - cosA;
      yb = y + cosA;
      yc = y;

      recRotate = Math.PI * 2;
      rx = this.x;
      ry = this.y - bessel;
    }

    if (this.y === this.yt && this.x > this.xt) {
      xt = x + cosC;
      xa = x;
      xb = x;
      xc = x + cosB;

      yt = y;
      ya = y - cosA;
      yb = y + cosA;
      yc = y;

      recRotate = -Math.PI;
      rx = this.x;
      ry = this.y + bessel;
    }

    this.t.ctx.beginPath();
    this.t.ctx.save();
    this.t.ctx.save();
    this.t.ctx.translate(rx, ry);
    this.t.ctx.rotate(recRotate);
    this.t.ctx.strokeStyle = 'transparent';
    this.t.ctx.rect(0, 0, rw, rh);
    this.t.ctx.stroke();
    this.t.ctx.restore();
    this.t.ctx.clip();
    this.t.ctx.beginPath();
    this.t.ctx.shadowColor = '#ffffff';
    this.t.ctx.shadowBlur = 10;
    this.t.ctx.strokeStyle = '#ffffff';
    this.t.ctx.fillStyle = '#ffffff';
    this.t.ctx.moveTo(x, y);
    this.t.ctx.bezierCurveTo(xa, ya, xc, yc, xt, yt);
    this.t.ctx.bezierCurveTo(xc, yc, xb, yb, x, y);
    this.t.ctx.stroke();
    this.t.ctx.fill();
    this.t.ctx.restore();
  }
}