function bounceOut(k) {
  if (k < (1 / 2.75)) {
    return 7.5625 * k * k;
  }
  else if (k < (2 / 2.75)) {
    return 7.5625 * (k -= (1.5 / 2.75)) * k + 0.75;
  }
  else if (k < (2.5 / 2.75)) {
    return 7.5625 * (k -= (2.25 / 2.75)) * k + 0.9375;
  }
  else {
    return 7.5625 * (k -= (2.625 / 2.75)) * k + 0.984375;
  }
}

export default class Ball {
  constructor(r, x, y, info, fills, t) {
    this.t = t;
    this.r = info.emphasis ? fills[0].r * 1 : 6;
    this.x = x || 40;
    this.y = y || 40;
    this.fills = fills.map(item => {
      if (info.emphasis) {
        return {
          ...item,
          r: item.r * 1
        };
      }
      return item;
    });
    this.clickColor = '#ff00ff';
    this.clicked = false;
    this.shadowBlur = 14;
    this.info = info;
    this.fontColor = info.label.fontColor || '#9DDDFF';
    this.colorOpacity = 0;
    this.colorSpeed = 0.01;
    if (this.info.emphasis) {
      this.waves = [
        {
          r: 0,
          opacity: 1,
        },
        {
          r: this.r * 0.6,
          opacity: 1,
        },
        {
          r: this.r * 1.2,
          opacity: 1,
        },
      ]
    }
    this.visible = false;
    this.startTime = null;
    this.hoverMul = 1;
  }
  draw(ctx, time) {
    if (this.y + this.r < this.t._bottomDistance) {
      return;
    }
    if (this.info.emphasis && !this.visible) {
      this.visible = true;
      this.t.emit('node:visible', this.info);
    }
    ctx.save();

    if (!ctx.tracker && this.waves) {
      let fillColor = this.fills[0].color;
      if (this.t.focusPoint) {
        if (!this.runStart) {
          fillColor = '#333333';
        }
      }
      this.waves.forEach(item => {
        item.r += 0.08;
        item.opacity = (1 - item.r / (1.8 * this.r)).toFixed(6);
        const color = this.colorRgb(fillColor, item.opacity);
        ctx.beginPath();
        ctx.save();
        ctx.strokeStyle = color;
        ctx.arc(this.x, this.y, this.r + item.r, 0, Math.PI * 2);
        ctx.stroke();
        if (item.r >= 1.8 * this.r) {
          item.r = 0;
        }
        ctx.restore();
      })
    }

    if (this.runStart) {
      let fillColor;
      var grd = ctx.createRadialGradient(this.x, this.y, this.r * 2 / 5, this.x, this.y, this.r * 2);
      grd.addColorStop(0, this.info.style.runFills[0]);
      grd.addColorStop(1, this.info.style.runFills[1]);
      fillColor = grd;
      ctx.save();
      ctx.beginPath();
      if (!ctx.tracker) {
        ctx.shadowColor = '#245d66';
        ctx.shadowBlur = this.r * 3;
      }
      ctx.fillStyle = ctx.tracker ? this.roundColor : fillColor;
      ctx.arc(this.x, this.y, this.r, 0, Math.PI * 2);
      ctx.fill();
      ctx.restore();
    } else {
      // 节点本身
      this.colorOpacity += this.colorSpeed;
      if (this.colorOpacity >= 1) {
        this.colorOpacity = 1;
      }
      // bounceOut
      if (this.hover && this.startTime) {
        let progress = ((time - this.startTime) / 300);
        if (progress <= 1) {
          this.hoverMul = 1 + 0.4 * bounceOut(progress);
        }
      } else {
        this.hoverMul = 1;
      }

      if (ctx.tracker) {
        ctx.save();
        ctx.beginPath();
        ctx.fillStyle = this.roundColor;
        ctx.arc(this.x, this.y, this.r * this.hoverMul, 0, Math.PI * 2);
        ctx.fill();
        ctx.restore();
      } else {
        this.fills.forEach((fill) => {
          let fillColor = this.colorRgb(fill.color, this.colorOpacity);
          if (this.t.focusPoint) {
            fillColor = this.colorRgb(fill.color, 0.2);
          }
          ctx.save();
          ctx.beginPath();
          if (fill.shadowColor) {
            ctx.shadowColor = this.colorRgb(fill.shadowColor, this.colorOpacity);
          }
          if (fill.shadowBlur) {
            ctx.shadowBlur = fill.shadowBlur;
          }
          ctx.fillStyle = fillColor;
          ctx.arc(this.x, this.y, fill.r * this.hoverMul, 0, Math.PI * 2);
          ctx.fill();
          ctx.restore();
        })
      }
    }
    // 文本
    if (!ctx.tracker && this.info.label.text) {
      const fontColor = this.info.emphasis ? this.info.label.focusColor : this.fontColor;
      let fillColor = this.colorRgb(fontColor, this.colorOpacity);
      if (this.t.focusPoint) {
        if (!this.runStart) {
          fillColor = '#333333';
        } else {
          fillColor = this.info.label.focusColor;
        }
      }
      ctx.beginPath();
      ctx.font = this.info.label.fontSize + "px Arial";
      ctx.fillStyle = fillColor;
      ctx.textAlign = "center";
      if (this.info.label.text.indexOf('\n') > -1) {
        const strs = this.info.label.text.split('\n');
        for (let i = 0, strsLength = strs.length; i < strsLength; i++) {
          let marginBottom = this.info.label.marginBottom;
          if (i > 0) {
            marginBottom = this.info.label.marginBottom + i * this.info.label.fontSize + 4;
          }
          ctx.fillText(strs[i], this.x, this.y + this.r + marginBottom);
        }
      } else {
        ctx.fillText(this.info.label.text, this.x, this.y + this.r + this.info.label.marginBottom);
      }
    }
    ctx.restore();
  }

  colorRgb(str, opacity) {
    let sColor = str.toLowerCase();
    if (sColor) {
      if (sColor.length === 4) {
        let sColorNew = "#";
        for (let i = 1; i < 4; i += 1) {
          sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
        }
        sColor = sColorNew;
      }
      const sColorChange = [];
      for (let i = 1; i < 7; i += 2) {
        sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
      }
      return "rgba(" + sColorChange.join(",") + "," + opacity + ")";
    } else {
      return sColor;
    }
  }

  hexify(color) {
    const values = color
      .replace(/rgba?\(/, '')
      .replace(/\)/, '')
      .replace(/[\s+]/g, '')
      .split(',');
    const a = parseFloat(values[3] || 1),
      r = Math.floor(a * parseInt(values[0]) + (1 - a) * 255),
      g = Math.floor(a * parseInt(values[1]) + (1 - a) * 255),
      b = Math.floor(a * parseInt(values[2]) + (1 - a) * 255);
    return "#" +
      ("0" + r.toString(16)).slice(-2) +
      ("0" + g.toString(16)).slice(-2) +
      ("0" + b.toString(16)).slice(-2);
  }

}