import Event from './event';
import Ball from './ball';
import Line from './line';
import Star from './star';
import Single from './single';
import { _isMobile } from '@/utils/util';

function componentToHex(c) {
  const hex = c.toString(16);
  return hex.length == 1 ? "0" + hex : hex;
}


function rgbToHex(r, g, b) {
  return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
}

// 贝塞尔动画
class Cubic {
  constructor(a, b, c, d) {
    this.px3 = 3 * a;
    this.px2 = 3 * (c - a) - this.px3;
    this.px1 = 1 - this.px3 - this.px2;
    this.py3 = 3 * b;
    this.py2 = 3 * (d - b) - this.py3;
    this.py1 = 1 - this.py3 - this.py2;
    this.epsilon = 1e-7;     // 目标精度
  }
  getX(t) {
    return ((this.px1 * t + this.px2) * t + this.px3) * t;
  }
  getY(t) {
    return ((this.py1 * t + this.py2) * t + this.py3) * t;
  }
  solve(x) {
    if (x === 0 || x === 1) {             // 对 0 和 1 两个特殊 t 不做计算
      return this.getY(x);
    }
    let t = x;
    for (let i = 0; i < 8; i++) {         // 进行 8 次迭代
      let g = this.getX(t) - x;
      if (Math.abs(g) < this.epsilon) {   // 检测误差到可以接受的范围
        return this.getY(t);
      }
      let d = (3 * this.px1 * t + 2 * this.px2) * t + this.px3;  // 对 x 求导
      if (Math.abs(d) < 1e-6) {           // 如果梯度过低，说明牛顿迭代法无法达到更高精度
        break;
      }
      t = t - g / d;
    }
    return this.getY(t);                 // 对得到的近似 t 求 y
  }
}

class Tree extends Event {
  constructor(data) {
    super();
    this.treeBaseData = Object.assign({
      container: undefined,
      width: 1920,
      height: 1080,
    }, data);
    this.graphs = {}; //缓存图形
    this.zIndexArr = []; // 层级队列
    this.zIndexLast = []; // 初始化层级备份
    this.canvas = undefined;
    this.hideCanvas = undefined;
    this.ctx = undefined;
    this.hideCtx = undefined;
    this.cubicBessel = new Cubic(0.25, 0.1, 0.25, 1);
    this.cubicEaseOut = new Cubic(0, 0, .58, 1);
    this.cubicLineSpeed = new Cubic(.17, .67, .46, .98);
    // 聚焦的节点
    this.focusPoint = null;
    // 上一次聚焦的节点
    this.oldFocusPoint = null;
    // 点击选中的节点
    this.selectedGraph = null;
    // 可以点击的节点
    this.clickAbleNodes = [];
    // 时间标量
    this.startTime = performance.now()
    // 动画进度
    this.progress = 0;
    // 过渡时间
    this.totalTime = 600;
    // hover 节点
    this.hoverPoint = null;
    this.zoom = 1;
    this.canvasZoom = 1;
    this.oldZoom = 1;
    this.translateData = {
      x: 0,
      y: 0
    }
    this.canvasTranslateData = {
      x: 0,
      y: 0
    }
    this.oldTranslateData = {
      x: 0,
      y: 0
    }
    this.ratio = 1;
    this._bottomDistance = null;

    this._canvasInit();
  }

  _canvasInit() {
    let container = this.treeBaseData.container;
    if (typeof container === 'string') {
      container = document.getElementById(container);
      this.canvas = container;
    }
    if (!container) {
      throw new Error(`invalid container!`)
    }
    this.hideCanvas = document.createElement('canvas');
    this.ctx = this.canvas.getContext('2d');
    this.hideCtx = this.hideCanvas.getContext('2d');
    // 原子画布标志
    this.hideCtx.tracker = true;
    this._resize();

    window.addEventListener('resize', () => {
      this._resize();
    }, false);

    this.canvas.addEventListener('mousedown', e => {
      if (this.progress !== 1) {
        return;
      }
      let pointX = e.offsetX * this.ratio, pointY = e.offsetY * this.ratio;
      let getHideColor = this.hideCtx.getImageData(pointX, pointY, 1, 1).data;
      const getHexColor = rgbToHex(getHideColor[0], getHideColor[1], getHideColor[2]);
      const graphsData = this.graphs[getHexColor];//选中的图形信息
      this.selectedGraph = getHexColor;
      // 更新动画时间戳
      this.startTime = performance.now();

      this.oldTranslateData.x = this.canvasTranslateData.x;
      this.oldTranslateData.y = this.canvasTranslateData.y;
      this.oldZoom = this.canvasZoom;
      if (!graphsData) {
        this.zIndexArr = [...this.zIndexLast];
        this.focusPoint = null;
        this.oldFocusPoint = null;
        this.translateData.x = 0;
        this.translateData.y = 0;
        this.zoom = 1;
        super.emit('node:click', null);
        return;
      }

      if (this.clickAbleNodes.includes(graphsData.info.id)) {
        graphsData.clicked = true;
        if (!this.focusPoint) {
          this.zoom = 2;
        } else {
          this.oldFocusPoint = this.focusPoint;
        }
        super.emit('node:click', graphsData.info.id);
        this.focusPoint = graphsData.info;
        this.translateData.x = this.treeBaseData.width * 2 / 3 - (graphsData.x * this.zoom);
        this.translateData.y = this.treeBaseData.height / 2 - (graphsData.y * this.zoom);
      } else {
        this.oldFocusPoint = this.focusPoint;
      }
      const lightArr = [];
      // 更新状态
      this.zIndexArr.forEach(item => {
        const graph = this.graphs[item];
        if (this.focusPoint) {
          if (graph.info && this.focusPoint.id === graph.info.id) {
            graph.runStart = true;
            lightArr.push(item);
          }
          if (graph.startPoint && this.focusPoint.pos.x === graph.startPoint.x && this.focusPoint.pos.y === graph.startPoint.y) {
            graph.runStart = true;
            lightArr.push(item);
            this.zIndexArr.forEach(item => {
              const graph1 = this.graphs[item];
              if (graph1.info && graph1.x === graph.endPoint.x && graph1.y === graph.endPoint.y) {
                graph1.runStart = true;
                lightArr.push(item);
              }
            })
          }
          if (graph.endPoint && this.focusPoint.pos.x === graph.endPoint.x && this.focusPoint.pos.y === graph.endPoint.y) {
            graph.runStart = true;
            lightArr.push(item);
            this.zIndexArr.forEach(item => {
              const graph1 = this.graphs[item];
              if (graph1.info && graph1.x === graph.startPoint.x && graph1.y === graph.startPoint.y) {
                graph1.runStart = true;
                lightArr.push(item);
              }
            })
          }
        }
      })
      let zn = [];
      let zl = [];
      let zp = [];
      let singleP = null;
      this.zIndexArr.forEach(item => {
        const graph = this.graphs[item];
        if (!lightArr.includes(item)) {
          if (graph instanceof Single) {
            singleP = item;
          } else {
            graph.runStart = false;
            zn.push(item);
          }
        } else {
          if (graph.info) {
            zp.push(item);
          } else {
            zl.push(item);
          }
        }
      })
      // 更新层级
      this.zIndexArr = [...zn, ...zl, ...zp, singleP];
      this.drawGraph();

    }, false)

    this.canvas.addEventListener('mousemove', e => {

      let pointX = e.offsetX * this.ratio, pointY = e.offsetY * this.ratio;
      let graphsData;
      let getHideColor = this.hideCtx.getImageData(pointX, pointY, 1, 1).data;
      const getHexColor = rgbToHex(getHideColor[0], getHideColor[1], getHideColor[2]);
      graphsData = this.graphs[getHexColor];//选中的图形信息
      if (graphsData && graphsData.info && this.clickAbleNodes.includes(graphsData.info.id)) {
        this.hoverPoint = getHexColor;
        graphsData.hover = true;
        if (!graphsData.startTime) {
          graphsData.startTime = performance.now();
        }
        this.canvas.style.cursor = 'pointer';
      } else {
        this.canvas.style.cursor = 'default';
        if (this.hoverPoint) {
          this.graphs[this.hoverPoint].hover = false;
          this.graphs[this.hoverPoint].startTime = null;
          this.hoverPoint = null;
        }
      }
    }, false)

    this.canvas.addEventListener('mouseup', () => {
      const graphsData = this.graphs[this.selectedGraph];//选中的图形信息
      if (!graphsData) {//没有选中图形
        return
      }

      graphsData.clicked = false;
      this.drawGraph();
    }, false)
  }

  _resize() {
    // 解决windows因为系统分辨率，导致 devicePixelRatio 小于1的问题,再兼容可能是小数的问题
    const floorDpr = Math.floor(window.devicePixelRatio) < 1 ? 1 : Math.floor(window.devicePixelRatio);
    const devicePixelRatio = window.devicePixelRatio ? floorDpr : 1;
    const backingStoreRatio = this.ctx.webkitBackingStorePixelRatio ||
      this.ctx.mozBackingStorePixelRatio ||
      this.ctx.msBackingStorePixelRatio ||
      this.ctx.oBackingStorePixelRatio ||
      this.ctx.backingStorePixelRatio || 1;
    this.ratio = devicePixelRatio / backingStoreRatio;
    // 解决iPhone 8P 报错：Canvas area exceeds the maximum limit (width * height > 16777216).
    if (_isMobile()) {
      this.ratio = 1;
    }

    let winWidth = window.innerWidth;
    let winHeight = window.innerHeight;
    winWidth = winWidth <= 1200 ? 1200 : winWidth;
    let scale = 1, marginLeft = 0, marginTop = 0;
    if (winWidth / winHeight > 16 / 9) {
      scale = winHeight / 1080;
      marginLeft = (winWidth - (scale * 1920)) / 2 + 'px';
      marginTop = 0;

    } else {
      scale = winWidth / 1920;
      marginLeft = 0;
      marginTop = (winHeight - (scale * 1080)) / 2 + 'px';
    }

    this.canvas.style.transformOrigin = 'left top';
    this.canvas.style.transform = `translate(${marginLeft}, ${marginTop}) scale(${scale}) `;

    this.hideCanvas.style.transformOrigin = 'left top';
    this.hideCanvas.style.transform = `translate(${marginLeft}, ${marginTop}) scale(${scale}) `;


    this.canvas.style.width = this.treeBaseData.width + "px";
    this.canvas.style.height = this.treeBaseData.height + "px";
    this.hideCanvas.style.width = this.treeBaseData.width + "px";
    this.hideCanvas.style.height = this.treeBaseData.height + "px";
    this.hideCtx.width = this.ctx.width = this.hideCanvas.width = this.canvas.width = this.treeBaseData.width * this.ratio;
    this.hideCtx.height = this.ctx.height = this.hideCanvas.height = this.canvas.height = this.treeBaseData.height * this.ratio;
    if (this._bottomDistance === null) {
      this._bottomDistance = this.treeBaseData.height - 100;
    }

    this.ctx.scale(this.ratio, this.ratio);
    this.hideCtx.scale(this.ratio, this.ratio);

    this.drawGraph();
  }

  data(data) {
    const { nodes, lines } = data;

    const mrTop = window.innerWidth * 0.078125 <= 94 ? 0 : window.innerWidth * 0.078125 / 2 - 30

    // 位置校准
    const calibrationX = 27;
    const calibrationY = mrTop;

    // 节点数据处理
    const nodeObj = {};
    nodes.forEach(item => {
      nodeObj[item.id] = item;
    })

    // 数据实例集合
    const shapes = [];
    lines.forEach(item => {
      item = {
        startPoint: {
          x: nodeObj[item.target.startNode].pos.x,
          y: nodeObj[item.target.startNode].pos.y,
        },
        endPoint: {
          x: nodeObj[item.target.endNode].pos.x,
          y: nodeObj[item.target.endNode].pos.y,
        },
        ...item,
      },

        item.startPoint = {
          x: item.startPoint.x + calibrationX,
          y: item.startPoint.y + calibrationY
        }
      item.endPoint = {
        x: item.endPoint.x + calibrationX,
        y: item.endPoint.y + calibrationY
      }

      shapes.push(new Line(item.startPoint, item.endPoint, item.style, new Star(item.startPoint, item.endPoint, this), this));
    })

    nodes.forEach(item => {
      const { x, y, r } = item.pos;
      item.pos = {
        x: x + calibrationX,
        y: y + calibrationY,
        r: r,
      }
      if (item.emphasis) {
        this.clickAbleNodes.push(item.id);
      }

      shapes.push(new Ball(item.pos.r, item.pos.x, item.pos.y, item, item.style.fills, this));
    })

    const single = new Single(630 + calibrationX, 700 + calibrationY, 932 + calibrationX, 151 + calibrationY, this);
    shapes.push(single);

    const lineList = [];
    const arcList = [];
    shapes.forEach((shape) => {
      //初始化图形及底色
      let roundColor = this.getRandomColor();
      while (this.graphs[roundColor]) {
        roundColor = this.getRandomColor();
      }
      shape.roundColor = roundColor;
      this.graphs[roundColor] = shape;
      if (shape.startPoint) {
        lineList.push(roundColor);
      } else {
        arcList.push(roundColor);
      }
    })
    this.zIndexArr.push(...lineList, ...arcList);
    // 缓存层级
    this.zIndexLast.push(...this.zIndexArr);
  }

  drawGraph(time) {
    if (this._bottomDistance > 2) {
      this._bottomDistance -= 3;
    }
    this.ctx.clearRect(0, 0, this.ctx.width, this.ctx.height);//清空画布
    this.hideCtx.clearRect(0, 0, this.hideCtx.width, this.hideCtx.height);//清空画布

    this.ctx.save();
    this.hideCtx.save();

    this.ctx.translate(this.canvasTranslateData.x, this.canvasTranslateData.y);
    this.hideCtx.translate(this.canvasTranslateData.x, this.canvasTranslateData.y);

    this.ctx.scale(this.canvasZoom, this.canvasZoom);
    this.hideCtx.scale(this.canvasZoom, this.canvasZoom);


    this.zIndexArr.forEach(item => {
      const graph = this.graphs[item];
      if (!this.focusPoint) {
        graph.runStart = false;
      }
      graph.draw(this.ctx, time);
      graph.draw(this.hideCtx, time);
    })

    this.ctx.restore();
    this.hideCtx.restore();
  }

  draw() {
    requestAnimationFrame(time => {
      this.drawGraph(time);
      this.draw();
      this.progress = ((time - this.startTime) / this.totalTime);
      if (this.progress > 1) {

        this.oldZoom = this.canvasZoom;

        this.progress = 1;
        return;
      }
      let processSolve = this.cubicBessel.solve(this.progress);

      if (this.focusPoint) {
        // 放大的时候跟都可点击的作区分
        if (this.zoom > this.canvasZoom && !this.oldFocusPoint) {
          // 放大
          this.canvasZoom = processSolve * (this.zoom - this.oldZoom) + 1;
        }
      } else {
        // 缩小
        this.canvasZoom = (this.oldZoom - this.zoom - processSolve * (this.oldZoom - this.zoom)) + 1;
      }
      this.canvasTranslateData.x = this.oldTranslateData.x + processSolve * (this.translateData.x - this.oldTranslateData.x);
      this.canvasTranslateData.y = this.oldTranslateData.y + processSolve * (this.translateData.y - this.oldTranslateData.y);

    });
  }

  render() {
    this.startTime = performance.now();
    this.draw();
  }

  clearForcus() {
    this.startTime = performance.now();
    this.oldTranslateData.x = this.canvasTranslateData.x;
    this.oldTranslateData.y = this.canvasTranslateData.y;
    this.oldZoom = this.canvasZoom;
    this.zIndexArr = [...this.zIndexLast];
    this.focusPoint = null;
    this.oldFocusPoint = null;
    this.translateData.x = 0;
    this.translateData.y = 0;
    this.zoom = 1;
    super.emit('node:click', null);
  }

  //随机生成16位的颜色值
  getRandomColor() {
    let color = '#';
    while (color.length < 7) {
      color += '0123456789abcdef'[Math.floor(Math.random() * 16)]
    }
    return color;
  }


}

export default Tree;
