<template>
  <div ref="homePage" class="home-page" :style="{marginTop: mrTop}">
    <transition name="fade">
      <div key="btn" v-if="!bgShow" class="skip-video" @click="skipVideo">跳过</div>
    </transition>
    <div v-if="canNotAutoplay && !bgShow" class="vide-btn" @click="PlayBtn"></div>
    <div class="video-box" :class="[bgShow ? 'bg-img' : '']">
      <transition name="fade">
        <video key="video" v-if="!bgShow" ref="video" class="video" autoplay muted>
          <source src="https://wenxiang-source.oss-cn-hangzhou.aliyuncs.com/wx_website.mp4" type="video/mp4">
        </video>
      </transition>
    </div>
    <div class="canvas-box">
      <div class="tips-box">
        <div v-if="someIncludes([9])" class="product base-in">
          <span class="content">基础类产品</span>
        </div>
        <div v-if="someIncludes([14, 15, 16, 17, 18])" class="product tool-in">
          <span class="content">工具类产品</span>
        </div>
        <div v-if="someIncludes([19])" class="product lead-in">
          <span class="content">引领性产品</span>
        </div>
      </div>
      <canvas id="canvas"></canvas>
      <transition name="product-contain">
        <div v-if="focusPoint" class="product-detail">
          <transition-group name="product">
            <template v-for="item in productList">
              <div v-if="focusPoint === item.id" class="product-detail-item" :key="item.id">
                <h2 class="product-title">{{item.title}}</h2>
                <h3 class="product-sub-title">{{item.subTitle}}</h3>
                <div class="product-img">
                  <img :src="item.img" alt="">
                </div>
                <p class="product-tip">{{item.tip}}</p>
                <div class="product-bottom">
                  <div></div>
                  <div class="back-btn">
                    <span>进入</span>
                    <div class="btn-arrow"></div>
                    <a :href="item.url"></a>
                  </div>
                </div>
              </div>
            </template>
          </transition-group>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import Bus from '@/common/bus.js';

import Tree from './canvas/tree';
import nodes from './data/nodes';
import lines from './data/lines';

export default {
  name: 'Home',
  data() {
    return {
      tree: null,
      touchPoints: [],
      bgShow: false,
      canvasRun: false,
      focusPointContain: false,
      focusPoint: null,
      productList: [
        {
          id: 9,
          title: '问向实验室 WX Labs',
          subTitle: ' ',
          img: require('@/assets/images/home/wxlab.jpg'),
          tip: '',
          url: '/wxLabs',
        },
        {
          id: 14,
          title: '家校共育系统',
          subTitle: ' ',
          img: require('@/assets/images/home/jxgy.jpg'),
          tip: '赋能家校合作 - 培养面向未来的孩子',
          url: '/familySchoolSystem',
        },
        {
          id: 15,
          title: '心理健康系统',
          subTitle: ' ',
          img: require('@/assets/images/home/xljk.jpg'),
          tip: '夯实毕生发展基础',
          url: '/metalSystem',
        },
        {
          id: 16,
          title: '生涯发展系统',
          subTitle: ' ',
          img: require('@/assets/images/home/syfz.jpg'),
          tip: '发掘6~18岁孩子正向成长潜能 - 提升孩子应变未来的能力',
          url: '/careerSys',
        },
        {
          id: 17,
          title: '教师发展系统',
          subTitle: ' ',
          img: require('@/assets/images/home/jsfz.jpg'),
          tip: '面向未来的教师发展',
          url: '/teacherDevSys',
        },
        {
          id: 18,
          title: '数据看板',
          subTitle: ' ',
          img: require('@/assets/images/home/sjkb.jpg'),
          tip: '',
          url: '/dataBoard',
        },
        {
          id: 19,
          title: '数字画像',
          subTitle: ' ',
          img: require('@/assets/images/home/szhx.jpg'),
          tip: '五育并举的科学指标和数据模型 - 为学校定位，为教师助力，为学生提效，为家长导航',
          url: '/digitalPortrait',
        }
      ],
      mrTop: '-7.8125vw',
      canNotAutoplay: false
    }
  },

  mounted() {
    if (window.__PRERENDER_INJECTED && window.__PRERENDER_INJECTED.foo && window.__PRERENDER_INJECTED.foo === 'bar') {
      return;
    }
    window.addEventListener('resize', this.videoResize);
    this.videoResize();
    const tree = new Tree({
      container: 'canvas'
    });
    this.tree = tree;
    tree.on('node:visible', info => {
      this.touchPoints.push(info.id);
    })
    tree.on('node:click', id => {
      this.focusPoint = id;
    }) 
    this.$refs.video.addEventListener('loadedmetadata', () => {
      const videoPromise = this.$refs.video.play();
      if (videoPromise) {
        videoPromise.catch(error => {
          console.log(error);
          this.canNotAutoplay = true;
        })
      }
    });
    this.$refs.video.addEventListener('timeupdate', (e) => {
      if (!this.canvasRun && e.currentTarget.currentTime.toFixed(0) == 14) {
        this.canvasRun = true;
        tree.data({
          nodes, 
          lines
        })
        tree.render();
      }
    });
    this.$refs.video.addEventListener('ended', () => {
      this.bgShow = true;
      Bus.$emit('show');
    }, false);
    // 满屏计算
    this.mrTop = window.innerWidth * 0.078125 <= 94 ? '-94px' : '-7.8125vw';
    // 移动端
    this.$refs.homePage.style.minHeight = window.innerHeight + 'px';
  },

  methods: {
    videoResize() {
      if (!this.$refs.video) return;
      let winWidth = window.innerWidth;
      let winHeight = window.innerHeight;
      winWidth = winWidth <= 1200 ? 1200 : winWidth;
      winHeight = winHeight <= 657 ? 675 : winHeight;
      let scale = 1, marginLeft = null, marginTop = null;

      if (winWidth / winHeight > 16 / 9) {
        scale = winWidth / 1920;
        marginLeft = 0;
        marginTop = (winHeight - (scale * 1080)) / 2 + 'px';
      } else {
        scale = winHeight / 1080;
        marginLeft = (winWidth - (scale * 1920)) / 2 + 'px';
        marginTop = 0;
      }

      this.$refs.video.style.transformOrigin = 'left top';
      this.$refs.video.style.transform = `translate(${marginLeft}, ${marginTop}) scale(${scale})`;
    },
    someIncludes(arr) {
      let res = false;
      for (let i=0;i<arr.length;i++) {
        if (this.touchPoints.includes(arr[i])) {
          res = true;
          break;
        }
      }
      return res;
    },
    skipVideo() {
      this.$refs.video.currentTime = this.$refs.video.duration;
      if (this.canvasRun) return;
      this.canvasRun = true;
      this.bgShow = true;
      Bus.$emit('show');
      this.tree.data({
        nodes, 
        lines
      })
      this.tree.render();
    },
    PlayBtn() {
      const videoPromise = this.$refs.video.play();
      if (videoPromise) {
        videoPromise.catch(error => {
          console.log(error);
        })
      }
    }
  }
}
</script>
