export default class Line {
  constructor(startPoint, endPoint, style, star, t) {
    this.t = t;
    this.startPoint = startPoint;
    this.endPoint = endPoint;
    this.style = style;
    this.speed = 1;
    this.progress = 0;
    this.xsub = Math.abs(this.startPoint.x - this.endPoint.x);
    this.ysub = Math.abs(this.startPoint.y - this.endPoint.y);
    this.sq = Math.sqrt(this.xsub * this.xsub + this.ysub * this.ysub);
    this.xSub = this.endPoint.x - this.startPoint.x;
    this.ySub = this.endPoint.y - this.startPoint.y;
    this.xt = this.startPoint.x;
    this.yt = this.startPoint.y;
    this.star = star;
    this.runStart = false;
    this.startTime = null;
  }
  draw(ctx, time) {
    if (ctx.tracker) return;
    if (this.startPoint.y < this.t._bottomDistance && this.endPoint.y < this.t._bottomDistance) {
      return;
    }
    if (!this.startTime) {
      this.startTime = performance.now();
    }
    this.progress = ((time - this.startTime) / 1000);

    if (this.progress >= 1) {
      this.progress = 1;
      this.xt = this.startPoint.x + this.xSub;
      this.yt = this.startPoint.y + this.ySub;
    } else {
      this.xt = this.startPoint.x + this.xSub * this.t.cubicLineSpeed.solve(this.progress);
      this.yt = this.startPoint.y + this.ySub * this.t.cubicLineSpeed.solve(this.progress);
    }

    ctx.save();//储存状态
    ctx.beginPath();
    ctx.moveTo(this.startPoint.x, this.startPoint.y);

    if (this.style) {
      ctx.lineWidth = this.style.lineWidth;
      ctx.strokeStyle = this.style.color;
      if (this.t.focusPoint) {
        if (this.runStart) {
          ctx.strokeStyle = '#FFFFFF';
        } else {
          ctx.strokeStyle = this.colorRgb(this.style.color, 0.2);
        }
      }
    }
    ctx.lineTo(this.xt, this.yt);
    ctx.stroke();
    ctx.restore();
    if (this.runStart && this.star) {
      this.star.draw();
    }
  }

  colorRgb(str, opacity) {
    let sColor = str.toLowerCase();
    if (sColor) {
      if (sColor.length === 4) {
        let sColorNew = "#";
        for (let i = 1; i < 4; i += 1) {
          sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
        }
        sColor = sColorNew;
      }
      const sColorChange = [];
      for (let i = 1; i < 7; i += 2) {
        sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
      }
      return "rgba(" + sColorChange.join(",") + "," + opacity + ")";
    } else {
      return sColor;
    }
  }
}