import canvasLogo from './canvas-logo';
export default class Single {
  constructor(startX, startY, endX, endY, t) {
    this.t = t;
    this.startX = startX - 60;
    this.startY = startY - 60;
    this.endX = endX - 60;
    this.endY = endY - 60;
    this.imgLoaded = false;
    this.img = new Image();
    this.img.src = canvasLogo;
    this.img.onload = () => {
      this.imgLoaded = true;
    }
    this.lastProgress = 0;
    this.lastStep = 1;
    this.ySub = this.startY - this.endY;
    this.positionProgress = 0;
    this.startTime = performance.now();
    this.r = 0;
    this.progress = 0;
  }

  draw(ctx, time) {
    this.progress = ((time - this.startTime) / 1000);
    let xp;
    if (this.progress <= 1) {
      this.positionProgress = this.ySub * this.t.cubicEaseOut.solve(this.progress);
      this.r = 60 * this.t.cubicEaseOut.solve(this.progress);
    }
    if (this.positionProgress >= this.startY - this.endY) {
      this.positionProgress = this.startY - this.endY;
    }
    xp = this.startY - this.positionProgress;

    ctx.save();
    this.animate(ctx);
    if (this.imgLoaded) {
      // 注意dpr
      ctx.drawImage(this.img, 0, 0, 284, 284, this.endX + (60 - this.r), xp + (60 - this.r), this.r * 2, this.r * 2);
    }
    ctx.restore();
  }

  animate(ctx) {
    if (this.endY < this.t._bottomDistance) {
      return;
    }
    this.lastProgress += this.lastStep;
    if (this.lastProgress > 40) {
      this.lastStep *= -1;
    }
    if (this.lastProgress < 2 && this.lastStep === -1) {
      this.lastStep = 0;
    }
    ctx.beginPath();
    ctx.shadowColor = '#6EDBF0';
    ctx.shadowBlur = this.lastProgress;
    ctx.fillStyle = '#6EDBF0';
    ctx.arc(this.endX + 60, this.endY + 60, 8, 0, Math.PI * 2);
    ctx.fill();
  }
}